@import "../../../variables";

.MosaicPromo {
    max-width: 140rem;
    margin: 2rem auto;

    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-gap: 1rem;

    @media only screen and (max-width: $bp-small) {
        grid-template-columns: 1fr;
    }

    & > article {

        &:nth-child(1) {
            grid-row: 1 / span 3;
            grid-column: 1 / 2;

            @media only screen and (max-width: $bp-small) {
                grid-row: unset;
                grid-column: unset;
            }
        }
    
        &:nth-child(2) {
            grid-row: 4 / span 3;
            grid-column: 1 / 2;

            @media only screen and (max-width: $bp-small) {
                grid-row: unset;
                grid-column: unset;
            }
        }
    
        &:nth-child(3) {
            grid-row: 1 / span 2;

            @media only screen and (max-width: $bp-small) {
                grid-row: unset;
                grid-column: unset;
            }
        }
    
        &:nth-child(4) {
            grid-row: 3 / span 2;

            @media only screen and (max-width: $bp-small) {
                grid-row: unset;
                grid-column: unset;
            }
        }
    
        &:nth-child(5) {
            grid-row: 5 / span 2;

            @media only screen and (max-width: $bp-small) {
                grid-row: unset;
                grid-column: unset;
            }
        }
    }
    
}