@import "../../variables";

.FilterPool {
    &__Header {
        display: flex;
        justify-content: space-between;
        padding-bottom: 1rem;
        border-bottom: 1px solid rgba($color-gray-dark, 0.5);
        font-size: 1.6rem;
        font-weight: 600;

        span {
            color: $color-tertiary-dark-1;
            cursor: pointer;
            transition: all 0.2s ease-out;

            &:hover {
                color: $color-complement-dark-1;
            }
        }
    }
}

